import React from "react";
import AwesomeSlider from "react-awesome-slider";

// @ts-ignore
import withAutoplay from "react-awesome-slider/dist/autoplay";

import "../styles/awesome-slider-styles.scss";
import slideshow_img_1 from "../assets/ext_img_hero_1_d.jpg";
import slideshow_img_2 from "../assets/ext_img_hero_2_d.webp";
import slideshow_img_3 from "../assets/ext_img_hero_3_d.webp";
import slideshow_img_4 from "../assets/ext_img_hero_4_d.webp";
import "../styles/hero-section.scss";

const AutoplaySlider = withAutoplay(AwesomeSlider);

const slider = (
  <AutoplaySlider
    play={true}
    cancelOnInteraction={false} // should stop playing on user interaction
    interval={3000}
    bullets={false}
    organicArrows={false}
    mobileTouch={false}
    showTimer={false}>
    <div data-src={slideshow_img_1} />
    <div data-src={slideshow_img_2} />
    <div data-src={slideshow_img_3} />
    <div data-src={slideshow_img_4} />
  </AutoplaySlider>
);

const HeroSlideShow = () => {
  return (
    <div className="resize-container">
      <div className="hero-slideshow-container">{slider}</div>
    </div>
  );
};

export default HeroSlideShow;
